@charset "UTF-8";
.detail {
  border-bottom: 75px solid transparent; }

/**
 *   轮播图的样式
 */
.carousel {
  margin-top: 0px; }

.carouselCard {
  height: 200px; }
  .carouselCard > img {
    width: 100%;
    height: 200px;
    vertical-align: top; }

.content {
  margin: 0 20px; }

.container {
  margin: 0 20px; }

.title {
  margin: 20px 0;
  font-size: 22px;
  color: #D0011B; }

.contentWrap {
  overflow: hidden; }

.viewMoreWrap {
  position: relative;
  width: 100%; }

.viewMore {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  width: 190px;
  height: 40px;
  border-radius: 20px;
  font-size: 14px;
  color: white;
  text-align: center;
  background-color: #E62A10; }
  .viewMore > span {
    line-height: 40px; }
  .viewMore > img {
    width: 20px;
    margin-left: 12px;
    vertical-align: middle; }

.gradient {
  position: absolute;
  left: 0;
  right: 0;
  top: -70px;
  height: 70px;
  background: -webkit-gradient(linear, left bottom, left top, from(white), to(rgba(255, 255, 255, 0.1)));
  background: linear-gradient(0deg, white, rgba(255, 255, 255, 0.1)); }

.item {
  padding-bottom: 10px;
  border-bottom: 1px solid #ccc; }

.activityTime {
  font-size: 16px;
  color: black; }

.activityLocation {
  font-size: 16px;
  color: black; }

.attention {
  font-family: PingFangSC-Light;
  font-size: 16px;
  color: black; }

.textOfWhiteSpaceDeal {
  white-space: pre-line; }

.tip {
  margin: 20px 0;
  font-size: 18px;
  color: #E62A10; }

.recommend {
  width: 100%;
  overflow: hidden; }

.recommendWarp {
  white-space: nowrap;
  overflow-y: hidden;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch !important; }

.recommendWarp::-webkit-scrollbar {
  display: none; }

.noTickets {
  position: fixed;
  left: 10px;
  right: 10px;
  bottom: 10px;
  height: 55px;
  padding: 12px 20px;
  font-family: PingFangSC-Regular;
  font-size: 16px;
  line-height: 31px;
  text-align: center;
  white-space: nowrap;
  background-color: #D6D6D6; }
  @media screen and (max-width: 370px) {
    .noTickets {
      padding: 12px 10px; } }



